.worklist ul{display:flex;justify-content:space-between;align-items:center;flex-wrap:wrap}.worklist ul li{width:45%;margin-bottom:45px}.worklist ul li a{display:inline-flex;justify-content:space-between}.worklist ul li a .cover img{width:100px;border-radius:5px}.worklist ul li a .workbrief{text-align:left;padding-left:15px}.worklist ul li a .workbrief h3{font-size:18px;color:#333}.worklist ul li a .workbrief p{padding:8px 0px;color:rgba(0,0,0,.7);line-height:1.8}.worklist ul li a .workbrief .tag{display:flex;justify-content:flex-start;flex-wrap:wrap}.worklist ul li a .workbrief .tag span{color:rgba(0,0,0,.55);font-size:14px}.worklist ul li a .workbrief .tag span:not(:last-child)::after{content:"•";padding:0px .5em}