.App{display:flex;min-height:100vh;flex-direction:column}.Content{flex:1}.Header{background-color:#736e6e;height:60px}.Header .selected{color:#d4baba !important;padding-bottom:5px;border-bottom:1.5px solid #d7d1d1}.Header .navigation_lab{width:70%;height:100%;margin:0px auto;padding:0px 12px;display:flex;align-items:center;justify-content:space-between}.Header .navigation_lab a:hover{text-decoration:none}.Header .navigation_lab .navigation_left{font-size:20px;color:#0b0b0b}.Header .navigation_lab .navigation_left::after{content:"◦◦◦";margin-left:.5em}.Header .navigation_lab .navigation_right{display:inline-flex;justify-content:flex-start}.Header .navigation_lab .navigation_right p{margin:0px 10px;font-size:14px;color:#0c0c0c}.Header .navigation_lab div{cursor:pointer}.Footer{text-align:center;padding:50px;color:#555;background-color:#fafafa;border-top:1px solid #a3a4a3}.brief{text-align:center}.brief h2{padding:12px 0px;font-size:20px}.brief img{width:150px;height:150px;border-radius:120px;margin-top:20px}.brief ul{display:flex;justify-content:center}.brief ul li{padding:6px 10px;background:rgba(87,199,255,.1);margin:12px 5px;border-radius:3px}.brief p{padding:12px 0px;color:rgba(0,0,0,.7)}