.wrap {
    flex: 1;

    .box {
        width: 650px;
        margin: 0px auto;
        padding: 40px 0px;

        .title {
            h1 {
                font-size: 24px;
            }

            p {
                color: #999;
                padding-top: 8px;
                font-size: 13px;

            }
        }

        .content {
            padding: 20px;

            p {
                padding-top: 10px;
                line-height: 2;
            }
        }

    }

    .tileimg {
        width: 350px;
        height: auto;

    }
    .shaderin {
        width: 350px;
        height: 180;
        box-shadow: 0 3px 1px 3px rgba(51, 51, 51, 0.5);
    }

    .shaderout {
        width: 350px;
        height: 180;
        box-shadow: 0 0 3px 1px rgba(51, 51, 51, 0.5);
    }
}